import { Container, Row, Col, Tab, Nav } from "react-bootstrap";
import { ProjectCard } from "./ProjectCard";
import space from "./space.png";
import threadline from "./threadline.png";
import password from "./password.png";
import tictactoe from "./tictactoe.png";
import manage from "./manage.png";
import chatter from "./chatter.png";

import colorSharp2 from "./color-sharp2.png";
//import 'animate.css';
import TrackVisibility from 'react-on-screen';

export const Projects = () => {

  const projects = [
    {
      title: "Space related website",
      description: "HTML, CSS & JavaScript",
      imgUrl: space,
      linkRef:'https://pmas98.github.io/Space-Tourism/'
    },
    {
      title: "Business Startup",
      description: "React & HTML & CSS",
      imgUrl: threadline,
      linkRef:'https://pmas98.github.io/Threadline/'
    },
    {
      title: "Password generator",
      description: "JavaScript & CSS",
      imgUrl: password,
      linkRef:'https://pmas98.github.io/PasswordGen/'
    },
    {
      title: "Tic Tac Toe game",
      description: "React & CSS",
      imgUrl: tictactoe,
      linkRef:'https://pmas98.github.io/TicTacToe/'
    },
    {
      title: "Software Startup",
      description: "HTML & CSS",
      imgUrl: manage,
      linkRef:'https://pmas98.github.io/Manage/'
    },
    {
      title: "Messaging website",
      description: "React & CSS & HTML & JavaScript",
      imgUrl: chatter,
      linkRef:'https://pmas98.github.io/Chatter/#/login'
    },
  ];

  return (
    <section className="project" id="projects">
      <Container>
        <Row>
          <Col size={12}>
            <TrackVisibility>
              {({ isVisible }) =>
              <div className={isVisible ? "animate__animated animate__fadeIn": ""}>
                <h2>Projects</h2>
                <p>Here are some of my personal projects! These are problems that i have either had the curiosity to learn how to solve or just to challenge myself .</p>
                <div className="skill-bx wow zoomIn">

                <Tab.Container id="projects-tabs" defaultActiveKey="first">
                  <Nav variant="pills" className="nav-pills mb-5 justify-content-center align-items-center" id="pills-tab">
                    <Nav.Item>
                      <Nav.Link eventKey="first">Web Dev</Nav.Link>
                    </Nav.Item>

                  </Nav>
                  <Tab.Content id="slideInUp" className={isVisible ? "animate__animated animate__slideInUp" : ""}>
                    <Tab.Pane eventKey="first">
                      <Row>
                        {
                          projects.map((project, index) => {
                            return (
                              <ProjectCard
                                key={index}
                                {...project}
                                />
                            )
                          })
                        }
                      </Row>
                    </Tab.Pane>
                    <Tab.Pane eventKey="second">
                    <Row>
                    {
                      projects.map((project, index) => {
                        return (
                          <ProjectCard
                            key={index}
                            {...project}
                            />
                        )
                      })
                    }
                    </Row>
                    </Tab.Pane>
                    <Tab.Pane eventKey="third">
                    <Row>
                    {
                      projects.map((project, index) => {
                        return (
                          <ProjectCard
                            key={index}
                            {...project}
                            />
                        )
                      })
                    }
                    </Row>
                    </Tab.Pane>
                  </Tab.Content>
                </Tab.Container>
                </div>
              </div>}
            </TrackVisibility>
          </Col>
        </Row>
      </Container>
      <img className="background-image-right" src={colorSharp2}></img>
    </section>
  )
}
