import { useState, useEffect } from "react";
import { Container, Row, Col } from "react-bootstrap";
import headerImg from "./header-img.svg";
import { ArrowRightCircle } from 'react-bootstrap-icons';
import TrackVisibility from 'react-on-screen';
import styled, {keyframes} from "styled-components";

export const Banner = () => {
  const [loopNum, setLoopNum] = useState(0);
  const [isDeleting, setIsDeleting] = useState(false);
  const [text, setText] = useState('');
  const [delta, setDelta] = useState(Math.random() * 300);
  const [index, setIndex] = useState(1);
  const toRotate = [ "Web Developer", "Data Scientist", "Programmer" ];
  const period = 100;

  useEffect(() => {
    let ticker = setInterval(() => {
      tick();
    }, 200);

    return () => { clearInterval(ticker) };
  }, [text])

  const tick = () => {
    let i = loopNum % toRotate.length;
    let fullText = toRotate[i];
    let updatedText = isDeleting ? fullText.substring(0, text.length - 1) : fullText.substring(0, text.length + 1);

    setText(updatedText);

    if (isDeleting) {
      setDelta(prevDelta => prevDelta);
    }

    if (!isDeleting && updatedText === fullText) {
      setIsDeleting(true);
      setIndex(prevIndex => prevIndex - 1);
      setDelta(period);
    } else if (isDeleting && updatedText === '') {
      setIsDeleting(false);
      setLoopNum(loopNum + 1);
      setIndex(1);
      setDelta(500);
    } else {
      setIndex(prevIndex => prevIndex + 1);
    }
  }

  const animation = keyframes`
    0% {transform:translateY(-100px);}
    25% {transform:translateY(-50px);}
    75% {transform:translateY(-25px);}
    100% {transform:translateY(0);}
`

  const Wrapper = styled.span`
    display: inline-block;
    animation-name:${animation};
    animation-duration:6s;
    animation-fill-mode: forwards;
`

  return (
    <section className="banner" id="home">
      <Container>
        <Row className="align-items-center">
          <Col xs={12} md={6} xl={7}>
            <TrackVisibility>
              {({ isVisible }) =>
              <div className={isVisible ? "animate__animated animate__fadeIn" : ""}>
                <span className="tagline">Welcome to my Portfolio</span>
                <h1>{`Hi! I'm Pedro, a `} <span className="txt-rotate" dataPeriod="100" data-rotate='[ "Web Dev", "Web Designer", "UI/UX Designer" ]'><span className="wrap">{text}</span></span></h1>
                  <p className="p-sizing">I'm a computer science undergraduate, with a degree in Chemical Engineering!</p>
                  <form action="?#connect">
                    <button type="submit" onClick={() => console.log('connect')}>Let’s talk! <ArrowRightCircle size={25} /></button>
                  </form>
              </div>}
            </TrackVisibility>
          </Col>
          <Col xs={12} md={6} xl={5}>
            <TrackVisibility>
              {({ isVisible }) =>
                <div className={isVisible ? "animate__animated animate__zoomIn" : ""}>
                  <img src={headerImg} alt="Header Img"/>
                </div>}
            </TrackVisibility>
          </Col>
        </Row>
      </Container>
    </section>
  )
}
