import meter1 from "./meter1.svg";
import meter2 from "./meter2.svg";
import meter3 from "./meter3.svg";
import aplus from "./aplus.svg";
import a from "./a.svg";
import bplus from "./bplus.svg";

import Carousel from 'react-multi-carousel';
import 'react-multi-carousel/lib/styles.css';
import arrow1 from "./arrowR.svg";
import arrow2 from "./arrow2.svg";
import colorSharp from "./color-sharp.png"

export const Skills = () => {
  const responsive = {
    superLargeDesktop: {
      // the naming can be any, depends on you.
      breakpoint: { max: 4000, min: 3000 },
      items: 5
    },
    desktop: {
      breakpoint: { max: 3000, min: 1024 },
      items: 3
    },
    tablet: {
      breakpoint: { max: 1024, min: 464 },
      items: 2
    },
    mobile: {
      breakpoint: { max: 464, min: 0 },
      items: 1
    }
  };

  return (
    <section className="skill" id="skills">
        <div className="">
            <div className="row">
                <div className="col-12">
                    <div className="skill-bx wow zoomIn">
                        <h2>Skills</h2>
                        <p>Here are some of my technical skills!</p>
                        <Carousel responsive={responsive} infinite={true} className="owl-carousel owl-theme skill-slider">
                            <div className="item">
                                <img src={aplus} alt="Image" />
                                <h5>Python</h5>
                            </div>
                            <div className="item">
                                <img src={bplus} alt="Image" />
                                <h5>Java Script</h5>
                            </div>
                            <div className="item">
                                <img src={a} alt="Image" />
                                <h5>React</h5>
                            </div>
                            <div className="item">
                                <img src={a} alt="Image" />
                                <h5>CSS</h5>
                            </div>
                            <div className="item">
                                <img src={aplus} alt="Image" />
                                <h5>Pandas</h5>
                            </div>
                            <div className="item">
                                <img src={bplus} alt="Image" />
                                <h5>Machine Learning</h5>
                            </div>
                            <div className="item">
                                <img src={aplus} alt="Image" />
                                <h5>PowerBI</h5>
                            </div>
                            <div className="item">
                                <img src={bplus} alt="Image" />
                                <h5>Tableau</h5>
                            </div>
                        </Carousel>
                    </div>
                </div>
            </div>
        </div>
        <img className="background-image-left" src={colorSharp} alt="Image" />
    </section>
  )
}
