import { Col } from "react-bootstrap";

export const ProjectCard = ({ title, description, imgUrl,linkRef }) => {
  return (
    <Col size={12} sm={6} md={4}>
      <div className="proj-imgbx">
        <img src={imgUrl} />
        <div className="proj-txtx">
          <h5 className="smol"><a href={linkRef}> {description} </a></h5>
        </div>
      </div>
    </Col>
  )
}
